<template>
  <div>
    <div v-loading="loading" style="overflow: auto; height: 900px">
      <el-row type="flex" justify="center" class="search-nav">
        <el-col :xs="20" :sm="18" :md="16" :lg="12" :xl="12">
          <el-card
            shadow="always"
            class="search-nav-el-card"
            :body-style="{ padding: 0 }"
          >
            <el-row type="flex" justify="space-around">
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.from"
                  placeholder="出发地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                  style="outline: none; border: none; box-shadow: none"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.to"
                  placeholder="目的地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-date-picker
                  v-model="search.date"
                  type="date"
                  placeholder="选择日期"
                  editable
                  style="width: 100%"
                  :clearable="true"
                >
                </el-date-picker>
              </el-col>
              <el-col :xs="4" :sm="1" :md="1" :lg="1" :xl="1">
                <el-button
                  icon="el-icon-search"
                  circle
                  @click="searchFilter"
                ></el-button>
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" style="margin: 20px 0" v-if="isShow">
        <el-col :xs="20" :sm="18" :md="16" :lg="14" :xl="14">
          <el-card
            shadow="always"
            class="filter-el-card"
            :body-style="{ padding: '5px' }"
          >
            <div
              style="
                display: flex;
                justify-content: end;
                align-items: center;
                height: 100%;
              "
            >
              <el-select
                v-model="listQuery.price"
                placeholder="价格"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in priceOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.time"
                placeholder="耗时"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in timeOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.depart"
                placeholder="起飞时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in departOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.arrive"
                placeholder="到达时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in arriveOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-button
                :circle="true"
                slot="reference"
                class="el-icon-search"
                @click="handlerFilter"
              ></el-button>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" class="nav" v-if="isShow">
        <el-col
          :xs="20"
          :sm="18"
          :md="16"
          :lg="14"
          :xl="14"
          v-infinite-scroll="loadLimit"
        >
          <el-card
            shadow="always"
            class="info-el-card"
            :body-style="{ padding: 0 }"
            v-for="(plane, index) in planesPart"
            :key="index"
          >
            <el-row
              type="flex"
              justify="space-between"
              align="middle"
              style="height: 120px"
            >
              <el-col :xs="5" :sm="4" :md="4" :lg="4" :xl="4">
                <div class="row">
                  <el-image
                    :src="require('@/assets/png/' + plane.str + '.png')"
                    style="
                      margin-right: 10px;
                      max-height: 50px;
                      max-width: 50px;
                    "
                  ></el-image>
                  <div class="col">
                    <h3>{{ planeDir[plane.str] }}</h3>
                    <div>EU2000</div>
                  </div>
                </div>
              </el-col>
              <el-col :xs="10" :sm="8" :md="10" :lg="10" :xl="10">
                <el-popover
                  placement="top"
                  title="航班信息"
                  width="300"
                  trigger="hover"
                >
                  <el-divider></el-divider>
                  <div>
                    <el-timeline>
                      <el-timeline-item
                        timestamp="4/12 08:00 北京首都机场"
                        placement="top"
                        color="#54c8f4"
                      >
                        <el-card>飞行时间 1 小时</el-card>
                      </el-timeline-item>
                      <el-timeline-item
                        timestamp="4/13 10:00 上海虹桥"
                        placement="top"
                        color="#54c8f4"
                      >
                      </el-timeline-item>
                    </el-timeline>
                  </div>
                  <div class="row" slot="reference">
                    <div class="col">
                      <h3>北京首都</h3>
                      <div>08:00</div>
                    </div>
                    <el-image
                      :src="require('@/assets/arrow.png')"
                      style="margin: 0 10px; width: 40px; height: 30px"
                    ></el-image>
                    <div class="col">
                      <h3>上海虹桥</h3>
                      <div>10:00</div>
                    </div>
                  </div>
                </el-popover>
              </el-col>
              <el-col :xs="5" :sm="3" :md="3" :lg="3" :xl="3">
                <h3 class="price" style="color: #409eff; font-size: 25px">
                  ￥1234
                </h3>
              </el-col>
              <el-col :xs="4" :sm="3" :md="3" :lg="3" :xl="3">
                <el-button
                  type="primary"
                  size="medium"
                  class="el-icon-shopping-cart-2"
                  @click="order(plane)"
                  >订购</el-button
                >
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
      <div
        style="
          display: flex;
          justify-content: center;
          align-items: center;
          position: fixed;
          left: 50%;
          bottom: 20px;
        "
      >
        <div
          style="height: 40px; width: 40px"
          v-loading="isInfiniteLoading"
        ></div>
      </div>
      <div class="loading" v-if="cardLoading"></div>
    </div>

    <el-dialog
      title="订单"
      :visible.sync="dialog"
      append-to-body
      fullscreen
      width="700px"
      customClass="customWidth"
    >
      <div class="col">
        <el-steps
          :active="active"
          simple
          style="width: 900px; background: #fff"
        >
          <el-step title="步骤 1"></el-step>
          <el-step title="步骤 2"></el-step>
          <el-step title="步骤 3"></el-step>
          <el-step title="步骤 4"></el-step>
        </el-steps>
        <el-divider></el-divider>
        <div style="width: 100%; height: 700px" v-if="active == 0">0</div>
        <div style="width: 100%; height: 700px" v-if="active == 1">1</div>
        <div style="width: 100%; height: 700px" v-if="active == 2">2</div>
        <div style="width: 100%; height: 700px" v-if="active == 3">3</div>
      </div>
      <el-button
        type="primary"
        style="position: absolute; bottom: 20px; right: 140px"
        @click="last"
        >上一步</el-button
      >
      <el-button
        type="primary"
        style="position: absolute; bottom: 20px; right: 20px"
        @click="next"
        >下一步</el-button
      >
    </el-dialog>
  </div>
</template>

