export default {
  data() {
    return {
      limit: 10,
      loading: true,
      cardLoading: false,
      isInfiniteLoading: false,
      options: constData.options,
      isShow: false,
      planeDir: data.planeInfo,
      planes: planes,
      planesPart: "",
      search: {
        from: "",
        to: "",
        date: "",
        pro: {
          expandTrigger: "hover",
        },
      },
      timeOptions: ["耗时升序", "耗时降序"],
      priceOptions: ["价格升序", "价格降序"],
      arriveOptions: ["到达时间升序", "到达时间降序"],
      departOptions: ["出发时间升序", "出发时间降序"],
      listQuery: {
        time: "",
        price: "",
        arrive: "",
        depart: "",
      },
      dialog: true,
      active: 0,
    };
  },
  methods: {
    ...mapMutations("login", {
      changeLoginDialog: "changeLoginDialog",
    }),
    loadLimit() {
      console.log("loadLimit");
      this.limit += 5;
      this.planesPart = this.planes.slice(0, this.limit);
      this.isInfiniteLoading = true;
      setTimeout(() => {
        this.isInfiniteLoading = false;
      }, 500);
    },
    searchFilter() {
      this.cardLoading = true;
      this.isShow = false;
      setTimeout(() => {
        this.cardLoading = false;
        this.isShow = true;
      }, 800);
    },
    handlerFilter() {
      console.log(this.listQuery);
    },
    order(row) {
      if (this.$store.state.login.loginState == false) {
        this.changeLoginDialog();
        this.$message({
          message: "请先登录",
          type: "warning",
        });
      } else {
        console.log(row);
        this.dialog = true;
      }
    },
    next() {
      if (this.active < 4) {
        this.active++;
      }
    },
    last() {
      if (this.active > 0) {
        this.active--;
      }
    },
  },
  activated() {
    this.planesPart = this.planes.slice(0, this.limit);
    setTimeout(() => {
      this.loading = false;
    }, 500);
  },
  deactivated() {
    this.loading = true;
    this.isShow = false;
  },
};
