<style scoped>
.search-nav {
  margin: 30px 0;
}
.search-nav-el-card {
  border-radius: 25px;
}
.search-nav-el-card:hover {
  box-shadow: 0 2px 15px 1px rgba(0, 0, 0, 0.2);
}
.el-button-plus {
  border: none;
  outline: none;
}
.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.nav {
  margin: 5px 0;
}
.info-el-card {
  overflow: visible !important;
  position: relative;
  min-height: 120px;
  margin: 5px 0;
}
.info-el-card::before,
.info-el-card::after {
  transform: translateX(0px);
}
.info-el-card:hover {
  box-shadow: 0 2px 12px 1px rgba(0, 0, 0, 0.3) !important;
}
.info-el-card:hover::before {
  content: "";
  position: absolute;
  top: 5%;
  left: -20px;
  width: 5px;
  height: 90%;
  background: #409eff;
  border-radius: 5px;
}
.info-el-card:hover::after {
  content: "";
  position: absolute;
  top: 5%;
  right: -20px;
  width: 5px;
  height: 90%;
  background: #409eff;
  border-radius: 5px;
}
.row {
  display: flex;
  justify-content: center;
  align-items: center;
}
.col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loading {
  display: block;
  position: relative;
  left: 50%;
  top: 40%;
  width: 6px;
  height: 10px;

  animation: rectangle infinite 1s ease-in-out -0.2s;

  background-color: #9a9b9c;
}

.loading:before,
.loading:after {
  position: absolute;
  width: 6px;
  height: 10px;
  content: "";
  background-color: #8d8b8b;
}

.loading:before {
  left: -14px;

  animation: rectangle infinite 1s ease-in-out -0.4s;
}

.loading:after {
  right: -14px;

  animation: rectangle infinite 1s ease-in-out;
}

@keyframes rectangle {
  0%,
  80%,
  100% {
    height: 20px;
    box-shadow: 0 0 #949292;
  }

  40% {
    height: 30px;
    box-shadow: 0 -20px #8f8e8e;
  }
}
</style>
